body {
  background: url("img3.9a1c3029.jpg") center / cover no-repeat;
}

.btn-6 {
  color: #b7ff00;
  z-index: 1;
  border: 1px solid #b7ff00;
  box-shadow: 0 0 5px #b7ff00, inset 0 0 5px #b7ff00;
}

.btn-6:after {
  content: "";
  width: 0;
  height: 100%;
  z-index: -1;
  background: #b7ff00;
  transition: all .3s;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: 0 0 20px #b7ff00;
}

.btn-6:hover {
  color: #000;
}

.btn-6:hover:after {
  width: 100%;
  left: 0;
}

.btn-6:active {
  top: 2px;
}

.form {
  width: 450px;
  background-color: rgba(0, 0, 0, .4);
  border: 3px solid #090909;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding: 25px 15px;
  display: flex;
}

.form label {
  color: #fff;
  flex-direction: column;
  margin-bottom: 15px;
  font-size: large;
  font-weight: 700;
  display: flex;
}

.form button {
  font: inherit;
  cursor: pointer;
  border: 2px solid #000;
  padding: 5px 10px;
  font-size: large;
  font-weight: 700;
}

.form input {
  border: 2px #000;
  outline: none;
  padding: 8px 12px;
}

